import { samplesDict } from './Tokens';
export default function tokenize(regex) {
    const str = regex.toString().slice(1, -1), samples = [];
    let bs = false;
    for (const char of str) {
        if (char === '\\') {
            bs = true;
            continue;
        }
        if (bs)
            if (char in samplesDict)
                samples.push(samplesDict[char]);
            else
                samples.push(eval(`"\\${char}"`));
        else if (char === '.')
            samples.push(samplesDict['.']);
        else
            samples.push([char]);
        bs = false;
    }
    return samples;
}
